// Angular components
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MatMenuModule, MatToolbarModule, MatDialogModule, MatProgressSpinnerModule  } from '@angular/material';
import { RouterModule, Routes } from '@angular/router';

// Third Party
import { CalendarModule } from 'angular-calendar';

// Components
import { AppComponent } from './app.component';
import { HomeComponent } from './views/home.component';
import { NavComponent } from './nav/nav.component';
import { InHomeServiceComponent } from './shared/inHomeService.component';
import { IMaFTServiceComponent } from './shared/IMaFT.component';
import { ClinicalServiceComponent } from './shared/clinicalService.component';
import { AboutMeComponent } from './views/aboutMe.component';
import { ServicesComponent } from './views/services.component';
import { HelpfulFormsComponent } from './views/helpfulForms.component';
import { RatesComponent } from './views/rates.component';
import { AppointmentsComponent } from './views/appointments.component';
import { SchedulerComponent } from './shared/scheduler.component';
import { PaymentsComponent } from './views/payments.component';
import { CommonQuestionsComponent } from './views/commonQuestions.component';
import { LinksComponent } from './views/links.component';
import { ContactMeComponent } from './views/contactMe.component';
import { PrivacyComponent } from './views/privacy.component';
import { ReadingsComponent } from './views/readings.component';

// Services
import { TextService } from './services/textService';
import { ApiService } from './services/apiService';
import { HttpClientModule } from '@angular/common/http';

export const routes : Routes = [
  { path: '', component: HomeComponent, pathMatch: 'full' },
  { path: 'aboutMe', component: AboutMeComponent, pathMatch: 'full' },
  { path: 'services', component: ServicesComponent, pathMatch: 'full' },
  { path: 'services/:id', component: ServicesComponent, pathMatch: 'full' },
  { path: 'helpfulForms', component: HelpfulFormsComponent, pathMatch: 'full' },
  { path: 'rates', component: RatesComponent, pathMatch: 'full' },
  { path: 'appointments', component: AppointmentsComponent, pathMatch: 'full' },
  { path: 'payments', component: PaymentsComponent, pathMatch: 'full' },
  { path: 'commonQuestions', component: CommonQuestionsComponent, pathMatch: 'full' },
  // { path: 'links', component: LinksComponent, pathMatch: 'full' },
  { path: 'contactMe', component: ContactMeComponent, pathMatch: 'full' },
  { path: 'privacy', component: PrivacyComponent, pathMatch: 'full' },
  // { path: 'readings', component: ReadingsComponent, pathMatch: 'full' }
]

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    NavComponent,
    InHomeServiceComponent,
    IMaFTServiceComponent,
    ClinicalServiceComponent,
    AboutMeComponent,
    ServicesComponent,
    HelpfulFormsComponent,
    RatesComponent,
    AppointmentsComponent,
    SchedulerComponent,
    PaymentsComponent,
    CommonQuestionsComponent,
    LinksComponent,
    ContactMeComponent,
    PrivacyComponent,
    ReadingsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    MatMenuModule,
    MatToolbarModule,
    MatDialogModule,
    MatProgressSpinnerModule,
    CalendarModule.forRoot({}),
    RouterModule.forRoot(routes)
  ],
  entryComponents: [
    SchedulerComponent
  ],
  providers: [
    TextService,
    ApiService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
