import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { TextService } from '../services/textService';

@Component({
    selector: 'inhome-services',
    templateUrl: './inHomeService.component.html',
    styleUrls: ['./inHomeService.component.scss']
})

export class InHomeServiceComponent implements OnInit {
    @Input() isHome:boolean = false;
    constructor (private _txtSvc: TextService, private _router:Router){}

    header: string;
    text: string;
    criteria: any;
    unionRegionalInfo: any;

    ngOnInit(): void {
        this.load();
    }

    load() {
        this._txtSvc.GetInHomeServicesModel().subscribe(data => {
            this.header = data.Header;
            this.criteria = data.Criteria;
            this.unionRegionalInfo = data.UnionRegionalInfo;
            if (!this.isHome){
                this.text = data.Text;
            }
            else {
                this.text = data.Text.substring(0,220) + '...';
            }
        });
    }
    routeToService() {
        this._router.navigateByUrl('services/3');
    }
}