import { Component, OnInit } from '@angular/core';
import { TextService } from '../services/textService';
import { CommonQuestions } from '../common/commonQuestionsViewModel';

@Component({
    templateUrl: './commonQuestions.component.html',
    styleUrls: [ './commonQuestions.component.scss' ]
})

export class CommonQuestionsComponent implements OnInit {
    constructor(private _textSvc:TextService) { }

    Header:string;
    Sections: CommonQuestions[] = [];
    ActiveSection: CommonQuestions;
    
    ngOnInit(): void {
        this.load();
    }
    load() {
        this.Header = 'Common Questions';
        this._textSvc.GetCommonQuestionsViewModel().subscribe(resp => {
            this.Sections = resp.sections;
        });
    }
    showDetails(s:CommonQuestions){
        let current: CommonQuestions[] = this.Sections.filter(x => x.IsActive);
        if (current && current.length){
            current[0].IsActive = false;
        }
        s.IsActive = true;
    }
}