import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { TextService } from '../services/textService';

@Component({
    selector: 'clinical-service',
    templateUrl: './clinicalService.component.html',
    styleUrls: ['./clinicalService.component.scss']
})

export class ClinicalServiceComponent implements OnInit {
    @Input() isHome: boolean;
    constructor(private _textSvc: TextService, private _router:Router) { }

    header: string;
    textItem: string;
    supervisionTypes:any[];
    summary: string;

    ngOnInit(): void {
        this.load();
    }

    load(){
        this._textSvc.GetClinicalServicesModel().subscribe(data => {
            this.header = data.Header;
            if (!this.isHome){
                this.textItem = data.Text;
                this.summary = data.Summary;
                this.supervisionTypes = data.SupervisionTypes;
            }   
            else {
                this.textItem = data.Text.substring(0,240) + '...';
            }
        });
    }
    routeToService() {
        this._router.navigateByUrl('services/2');
    }
}