import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { TextService } from '../services/textService';

@Component({
    selector: 'imaft-services',
    templateUrl: './IMaFT.component.html',
    styleUrls: ['./IMaFT.component.scss']

})

export class IMaFTServiceComponent implements OnInit {
    @Input() isHome:boolean;

    constructor(private _textSvc:TextService, private _router:Router){ }

    textItems:string[] = [];
    header: string;
    treatmentSpecializations: any;
    summary: string;

    ngOnInit(): void {
        this.load();
    }

    load() {
        this._textSvc.GetIMaFTServicesModel().subscribe(data => {
            this.header = data.Header;
            if (!this.isHome){
                this.textItems = data.Text;
                this.treatmentSpecializations = data.TreatmentSpecializations;
                this.summary = data.Summary;
            }
            else {
                this.textItems = [data.Text[0].substring(0,250) + '...'];
            }
        });
    }
    routeToService() {
        this._router.navigateByUrl('services/1');
    }
}