import { Component, OnInit } from '@angular/core';
import { MatMenu, MatToolbar } from '@angular/material';
import { TextService } from '../services/textService';

@Component({
    selector: 'home-component',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
    constructor(private _textSvc: TextService) {
    }

    private homeText: string;
    private boldText: string;
    private phone: string;
    private email: string;
    private fax:string;
    private address:string;
    private city:string;
    private state:string;
    private zip:string;
    private businessName:string;

    ngOnInit(): void {
        this.load();
    }

    load() {
        this._textSvc.GetHomeViewModel().subscribe(data => {
            this.homeText = data.Text;
            this.boldText = data.BoldText;
            this.phone = data.Phone;
            this.email = data.Email;
            this.fax = data.Fax;
            this.address = data.Address;
            this.city = data.City;
            this.state = data.State;
            this.zip = data.Zip;
            this.businessName = data.BusinessName;
        });
    }
}