import { Component, OnInit } from '@angular/core';
import { TextService } from '../services/textService';

@Component({
    selector: 'about-me-component',
    templateUrl: './aboutMe.component.html',
    styleUrls: [ './aboutMe.component.scss' ]
})

export class AboutMeComponent implements OnInit {
    constructor(private _textSvc: TextService) { }

    aboutMeText: string[] = [];
    education: any[] = [];
    licCertsAwards: string[] = [];
    additionalTraining: string[] = [];

    ngOnInit(): void {
        this.load();
    }
    load() {
        this._textSvc.GetAboutMeViewModel().subscribe(data => {
            this.aboutMeText = data.AboutMeText;
            this.education = data.Education;
            this.licCertsAwards = data.LicenseCertsAwards;
            this.additionalTraining = data.AdditionalTraining;
        });
    }
}