import { Component, OnInit } from '@angular/core';
import { TextService } from '../services/textService';
@Component({
    selector: 'helpful-forms-component',
    templateUrl: './helpfulForms.component.html',
    styleUrls: [ './helpfulForms.component.scss' ]
})

export class HelpfulFormsComponent implements OnInit {
    constructor(private _textSvc: TextService) { }

    text: string;
    forms: any[] = [];
    coordinateCare: any;
    pdfLink: any;

    ngOnInit(): void {
        this.load();
    }
    load() {
        this._textSvc.GetHelpfulFormsViewModel().subscribe(data => {
            this.text = data.FormsToBring.Text;
            this.forms = data.FormsToBring.Forms;
            this.coordinateCare = data.FormsToBring.CoordinateCare;
            this.pdfLink = data.FormsToBring.PDFLink;
        });
    }
}