import { Component, OnInit } from '@angular/core';
import { TextService } from '../services/textService';
import { Router } from '@angular/router';

@Component({
    selector: 'services-component',
    templateUrl: './services.component.html',
    styleUrls: [ './services.component.scss']
})

export class ServicesComponent implements OnInit {
    constructor(private _textSvc:TextService, private _router:Router) { }

    serviceTypes:string[] = [];
    currentView:string = '';
    
    ngOnInit(): void {
        this.load();
    }
    load() {
        this._textSvc.GetServicesViewModel().subscribe(data => {
            this.serviceTypes = data;
            let view = this._router.url.substr(this._router.url.lastIndexOf('/') + 1);
            switch (view){
                case '1':
                    this.currentView = 'ICFaMT';
                    break;
                case '2': 
                    this.currentView = 'Clinical';
                    break;
                case '3':
                    this.currentView = 'InHome';
                    break;
                default:
                    this.currentView = '';
                    break;  
            }
        });
    }
    toggleService(service:string){
        switch (service){
            case 'Individuals, Couples, Family, and Maritial Therapy':
                this.currentView = 'ICFaMT';
                break;
            case 'Clinical Supervision':
                this.currentView = 'Clinical';
                break;
            case 'In Home Therapy':
                this.currentView = 'InHome';
                break;
            default:
                this.currentView = '';
                break;
        }
    }
}